body {
    margin: 0;
    margin-top: 5em;
    color: $font-color;
    font-family: $font-style;
    font-size: 1.1em;
    line-height: 1.6;
    -webkit-font-smoothing: antialiased;
}
h1,
h2,
h3 {
    font-weight: 600;
    color: $heading-font-color;
    font-family: $heading-font;
    line-height: 1.5;
    margin-bottom: 3px;
}
h1 {
    color: $main;
    font-size: 2.5em;
    a,
    a:visited {
        text-decoration: none;
        color: $main;
    }
}
h2 {
    font-size: 2em;
}
a,
a:visited {
    color: $link-color;
    text-decoration: underline;
    font-weight: bold;
}
a:hover {
    color: $link-hover-color;
    text-decoration: underline;
}
