img {
    max-width: 100%; /* Ensure the image doesn't exceed its container */
    height: auto; /* Maintain the image's aspect ratio */
}

h1 {
    font-size: 2m; /* Adjust the font size as needed */
    margin-top: 10px; /* Adjust the top margin as needed */
}

/* Media query for smaller screens */
@media screen and (max-width: 600px) {
    img {
        max-width: 80%; /* Adjust for smaller screens */
    }

    h1 {
        font-size: 1.5em; /* Adjust the font size for smaller screens */
    }
}

main {
    padding: 0 15px;
    //  text-align: center;
    max-width: $content-width;
    margin: 0 auto;
}
time {
    color: #898989;
}
.container {
    max-width: $content-width;
    margin: 0 auto;
    padding-bottom: 50px; /* Add padding at the bottom of the container */
}
.contact-content {
    margin-bottom: 50px; /* Add bottom margin to create padding at the bottom */
}
.contained-image {
    max-width: 600px;
    width: 100%;
    height: auto;
    display: block;
    margin: 0 auto; /* Optional: Center the image horizontally */
}
.responsive-image {
    max-width: 100%;
    width: 100%;
    max-width: 600px; /* Adjust this to your desired maximum width */
    height: auto;
    display: block;
    margin: 0 auto; /* Optional: Center the image horizontally */
}

.post-navigation {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
}

.prev,
.next {
    width: 30%; /* Adjust as needed */
}

.instagram-media {
    max-width: 25%;
    margin: 0 auto;
}

/* Aside */

aside {
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    width: 100%;
    z-index: 2;
    border-bottom: 1px solid lighten($light, 30%);
    box-shadow: 0px 1px 1px RGBA(4, 25, 54, 0.1);

    nav {
        float: left;
        max-width: 800px;
        margin: 0 auto;

        ul {
            margin: 0;
            padding: 0;
            list-style: none;

            li {
                float: left;
                position: relative;

                a {
                    text-decoration: none;
                    display: block;
                    padding: 15px;
                    font-family: "Muli", sans-serif;
                    line-height: 40px;
                    margin-bottom: -1px;
                    box-shadow: 0;

                    &:hover,
                    &:active {
                        background: #ffffff;
                        text-decoration: none;
                        color: inherit;
                    }
                }
            }

            /* Add styles for the logo */
            .logo {
                float: left; /* Align the logo to the left */
                margin-right: 20px; /* Adjust the margin as needed */
                margin-top: 0px; /* Add margin to separate the logo from the navigation */

                a {
                    display: block; /* Make the anchor a block-level element to cover the entire logo */

                    /* Remove hover effect on the logo */
                    &:hover {
                        background: none;
                        color: inherit;
                    }
                }

                img {
                    width: 50px; /* Explicitly set width to the desired square size */
                    height: 50px; /* Explicitly set height to the desired square size */
                    object-fit: contain; /* Ensure the image fits within the square without distortion */
                    vertical-align: middle; /* Align the logo vertically in the middle with the text */
                }
            }
            .header-spacer {
                height: 20px; /* Adjust the height for the desired padding */
                width: 100%; /* Make it take the full width */
            }
        }

        &:after {
            content: "";
            display: table;
            clear: both;
        }
    }
}

footer {
    border-top: 1px solid #ccc;
    text-align: center;
    padding-top: 10px;
}
